<template>
  <aside>
    <h1>{{$store.getters.get_organization}}</h1>
    <treatments></treatments>
  </aside>
  <main>
    <h1>{{$store.getters.get_location}}</h1>
    <iframe :src="$store.getters.get_url[1]" frameBorder="0"></iframe>
  </main>
  <modalwindow v-if="modal" :ticket="$store.getters.get_first_notification.ticket"
  :operator="$store.getters.get_first_notification.operator"></modalwindow>
</template>

<script>
import axios from 'axios'
import modalwindow from './components/modalwindow.vue'
import treatments from './components/treatrments.vue'
export default {
  name: 'App',
  components: {
    modalwindow,
    treatments
  },
  data() {
    return {
      modal: false,
    }
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      axios.get(this.$store.getters.get_url[0]).then(resp => {
        this.$store.commit('set_location', resp.data.location);
        this.$store.commit('set_device', resp.data.device);
        this.$store.commit('set_organization', resp.data.organization);
        console.log(resp.data)
      });
    },
    show_modal() {
      if (this.$store.getters.get_notification.length > 0 && this.modal == false) {
        this.modal = true;
        setTimeout(() => {
          this.modal = false;
          this.$store.commit('del_notification');
          this.show_modal();
        }, 3000)
      }
    },
  },
  sockets: {
    call_client(data) {
      this.$store.commit('add_notification',  data);
      this.show_modal();
    },
    state(data) {
      this.$store.commit('set_state',  data);
    }
  }
}
</script>

